.connectWithFellow {
  align-self: stretch;
  position: relative;
  font-weight: 500;
}
.content,
.descriptions {
  display: flex;
  flex-direction: column;
}
.content {
  width: 271px;
  align-items: flex-start;
  justify-content: flex-start;
}
.descriptions {
  border-radius: var(--br-xs) var(--br-xs) 0 var(--br-xs);
  background-color: var(--color-royalblue);
  padding: var(--padding-5xs);
  align-items: center;
  justify-content: center;
}
.profileChild {
  position: absolute;
  top: -1px;
  left: -1px;
  border-radius: var(--br-xs);
  width: 26px;
  height: 26px;
  object-fit: cover;
}
.checkVerified02Child,
.profile {
  position: absolute;
  background-color: var(--color-white);
}
.profile {
  top: 0;
  left: 0;
  border-radius: var(--br-980xl);
  width: 24px;
  height: 24px;
  overflow: hidden;
}
.checkVerified02Child {
  height: 33.33%;
  width: 41.67%;
  top: 33.33%;
  right: 25%;
  bottom: 33.33%;
  left: 33.33%;
}
.solidIcon {
  position: absolute;
  height: 91.67%;
  width: 91.67%;
  top: 4.17%;
  right: 4.17%;
  bottom: 4.17%;
  left: 4.17%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.checkVerified02 {
  position: absolute;
  top: 16.36px;
  left: 16.36px;
  border-radius: var(--br-980xl);
  width: 7.64px;
  height: 7.64px;
}
.profileParent {
  position: relative;
  width: 24px;
  height: 24px;
  display: none;
}
.descriptionsParent {
  position: absolute;
  top: 414px;
  left: 68px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xs);
  text-align: left;
  font-size: var(--font-size-sm);
  color: var(--color-white);
  font-family: var(--font-mulish);
}
