@import url("https://fonts.googleapis.com/css2?family=Lato:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Mulish:wght@400;500;600;700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@400&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-open-sans: "Open Sans";
  --body-regular: Poppins;
  --font-mulish: Mulish;
  --default-body-bold: Lato;

  /* font sizes */
  --font-size-8xl: 27px;
  --font-size-3xl: 22px;
  --font-size-base: 16px;
  --body-regular-size: 17px;
  --font-size-sm: 14px;
  --font-size-lg: 18px;
  --font-size-5xl: 24px;

  /* Colors */
  --color-whitesmoke-100: #faf9f4;
  --system-colours-default-colors-systemwhiteblack-dark: #000;
  --color-dimgray-100: #606060;
  --color-dimgray-200: #50555c;
  --color-gray-100: #fcfcfe;
  --color-gray-200: #141e0d;
  --system-colours-default-colors-systemgrey-2-light: #aeaeb2;
  --color-white: #fff;
  --color-silver: #b7b7b7;
  --color-gainsboro: #e5e5e0;
  --color-royalblue: #1c63d5;

  /* Gaps */
  --gap-base: 16px;
  --gap-xs: 12px;
  --gap-5xs: 8px;

  /* Paddings */
  --padding-xs: 12px;
  --padding-base: 16px;
  --padding-5xs: 8px;
  --padding-2xs: 11px;
  --padding-6xs: 7px;
  --padding-xl: 20px;

  /* border radiuses */
  --br-980xl: 999px;
  --br-81xl: 100px;
  --br-8xs-6: 4.6px;
  --br-12xs: 1px;
  --br-5xs: 8px;
  --br-xs: 12px;
  --br-29xl: 48px;
}
